.career {
    padding: 100px 0 64px 0;
    width: 100%;
}

.career .container { max-width: var(--max-width); margin: 0 auto; padding: 0 20px; }

.career_title { padding-top:0; margin-bottom: 24px; font-size: 40px; font-weight: 800; text-align:center; color:var(--accent); }

.career-section { display: grid; grid-template-columns: 280px 1fr; gap: 28px; align-items: start; }
.career-left { background: var(--card); padding: 18px; border-radius: 12px; box-shadow: var(--soft-shadow); }
.career-right { background: transparent; }

.chip { display:inline-block; padding:6px 10px; border-radius:8px; font-weight:600; font-size:13px; }
.chip.red { color: #ff5b7a; background: rgba(241,103,114,0.06); }
.chip.blue { color: #2b6f9e; background: rgba(43,111,158,0.06); }
.chip.green { color: #07b343; background: rgba(7,179,59,0.06); }

.resume-item h3 { font-size: 18px; margin: 0 0 10px 0; }

.subtitle { font-size: 18px; margin-bottom: 8px; }

.career_wl h2 { font-size: 20px; margin-bottom: 6px; }
.career_wl h3 { font-size: 18px; margin: 0; }
.career_wl h4 { font-size: 16px; margin: 0; }
.career_wr h3 { font-size: 16px; margin: 0 0 6px 0; }
.career_wr h4 { font-size: 14px; margin: 0 0 6px 0; }
.career_wr h6 { font-size: 14px; background: rgba(43,111,158,0.06); padding: 5px 12px; display: inline-block; font-weight: 600; margin: 6px 0 12px 0; border-radius: 6px; }

.table { border-collapse: collapse; border-top: 3px solid var(--accent); margin: 0 auto; width:100%; }
.table th { color: var(--accent); background: transparent; text-align: left; }
.table th, .table td { padding: 10px; border: 1px solid #f1f1f1; }

@media (max-width: 900px) {
    .career-section { grid-template-columns: 1fr; }
    .career_title { font-size: 32px; }
    .table th, .table td { font-size: 14px; }
}

/* timeline */
.timeline { position: relative; padding-left: 20px; }
.timeline:before { content: ''; position: absolute; left: 12px; top: 0; bottom: 0; width: 2px; background: rgba(43,111,158,0.08); }
.timeline-item { position: relative; padding: 18px 0 18px 36px; }
.timeline-item:before { content: ''; position: absolute; left: 6px; top: 24px; width: 12px; height: 12px; border-radius: 50%; background: var(--accent); box-shadow: 0 2px 6px rgba(17,24,39,0.06); }
.timeline-item h4 { margin: 0 0 6px 0; font-size: 16px; }
.timeline-item p { margin: 0; color: var(--muted); }

p { margin-top: 0px; }

.mb20 { margin-bottom: 20px; }

.line { border-bottom: 0.5px solid rgba(0,0,0,0.08); margin: 30px 0 0; }

.modal-body li { max-width: 100%; width: 100%; white-space: pre-wrap; word-break: break-word; caret-color: rgb(55, 53, 47); padding: 3px 2px; text-align: left; font-weight: 500; }

/* Experience timeline layout (MeganMagic-inspired) */
.experience { padding: 10px 0 0 0; }
.exp-row { display: grid; grid-template-columns: 160px 28px 1fr; gap: 18px; align-items: start; padding: 18px 0; position: relative; }
.exp-year { font-weight: 700; color: var(--muted); font-size: 14px; display: flex; align-items: center; }
.exp-line { position: relative; width: 2px; background: rgba(43,111,158,0.08); height: 100%; margin-top: 4px; }
.exp-line:after { content: ''; position: absolute; left: 50%; transform: translateX(-50%); top: 6px; width: 12px; height: 12px; border-radius: 50%; background: var(--accent); box-shadow: 0 6px 18px rgba(14,30,70,0.06); }
.exp-content { padding-bottom: 8px; }
.exp-card { background: var(--card); padding: 18px; border-radius: 12px; box-shadow: var(--soft-shadow); transition: transform .18s ease, box-shadow .18s ease; }
.exp-row:hover .exp-card { transform: translateY(-6px); box-shadow: var(--shadow); }
.exp-header { display:flex; justify-content:space-between; gap:12px; align-items:flex-start; }
.exp-title { margin:0; font-size:16px; font-weight:800; }
.exp-meta { font-size:13px; color:var(--muted); }
.exp-summary { color:var(--muted); margin:8px 0 0 0; }

.team-badge { display:inline-block; margin-top:8px; background: rgba(43,111,158,0.08); color:var(--accent); padding:6px 10px; border-radius:999px; font-weight:700; font-size:12px; }

.exp-actions { margin-top:12px; display:flex; gap:12px; }
.exp-btn { background: var(--accent); color: white; border:0; padding:8px 12px; border-radius:999px; font-weight:700; cursor:pointer; box-shadow: 0 8px 18px rgba(6,110,158,0.08); transition: transform .12s ease, box-shadow .12s ease; }
.exp-btn:hover { transform: translateY(-3px); }
.exp-btn.ghost { background: transparent; color: var(--accent); box-shadow: none; border: 1px solid rgba(43,111,158,0.08); }

.exp-details { max-height: 0; overflow: hidden; opacity: 0; transition: max-height .36s ease, opacity .28s ease; }
.exp-details.show { max-height: 800px; opacity: 1; margin-top:12px; }
.detail-block { background: rgba(0,0,0,0.02); padding:12px; border-radius:8px; margin-bottom:10px; }
.detail-block h5 { margin:0 0 8px 0; font-size:14px; }
.detail-block ul { margin:0; padding-left:18px; }
.detail-block li { margin-bottom:6px; color:var(--muted); }

@media (max-width: 820px) {
    .exp-row { grid-template-columns: 96px 20px 1fr; }
    .exp-year { font-size:13px; }
}

@media (max-width: 520px) {
    .exp-row { grid-template-columns: 1fr; }
    .exp-line { display:none; }
    .exp-year { order: -1; margin-bottom:8px; }
}

/* company header toggle - balanced, visible but subtle */
.company-toggle-btn { display:inline-flex; align-items:center; justify-content:center; width:36px; height:36px; padding:0; border-radius:8px; border:1px solid rgba(43,111,158,0.12); background:transparent; color:var(--accent); cursor:pointer; transition:background .12s ease, transform .12s ease; }
.company-toggle-btn:hover { background: rgba(43,111,158,0.04); transform:translateY(-2px); }
.company-toggle-btn:focus { outline: 3px solid rgba(43,111,158,0.12); outline-offset: 3px; }
.company-toggle-btn .caret { font-size: 14px; display:inline-block; transition: transform .18s ease; }
.company-toggle-btn[aria-expanded="true"] { background: rgba(43,111,158,0.08); border-color: rgba(43,111,158,0.18); }
.company-toggle-btn[aria-expanded="true"] .caret { transform: rotate(90deg); }

.career_wl { display:flex; align-items:center; gap:10px; }

/* place the small toggle under the date */
.career_wr > div { margin-top: 8px; }

/* wrapper for small toggle + label */
.company-toggle-wrapper { display:flex; gap:8px; align-items:center; }
.company-toggle-label { background: transparent; border: none; color: var(--muted); font-weight:700; cursor: pointer; padding:6px 8px; border-radius:6px; transition: color .12s ease, background .12s ease, transform .12s ease; }
.company-toggle-label:hover { color: var(--accent); background: rgba(43,111,158,0.04); transform: translateY(-1px); }
.company-toggle-label:focus { outline: 3px solid rgba(43,111,158,0.08); outline-offset: 3px; }

@media (max-width: 540px) {
    .company-toggle-label { display: none; }
}

/* small arrow after the label (rotates when expanded) */
/* replace text ::after caret with inline SVG chevron for crisper visuals */
.company-toggle-label { display: inline-flex; align-items: center; gap: 8px; }
.company-toggle-label .chevron { width: 18px; height: 18px; display: inline-flex; align-items: center; justify-content: center; transition: transform .24s cubic-bezier(.2,.9,.2,1), opacity .18s ease; color: var(--muted); opacity: 0.95; }
.company-toggle-label.open .chevron { transform: rotate(180deg) scale(1.06); color: var(--accent); opacity: 1; }

/* let JS/GSAP control the collapsed state so we can animate height smoothly */
.experience.collapsed { /* no display:none here; GSAP will toggle visibility */ }